import keymirror from './ext/keymirror';

const GraphqlTypes = keymirror({
	GraphQLSchema: null,
	GraphQLObjectType: null,
	GraphQLInt: null,
	GraphQLFloat: null,
	GraphQLString: null,
	GraphQLBoolean: null,
	GraphQLID: null
});

export default GraphqlTypes;


